import React, { useState } from "react";
import Header from "./commonComponent/header";
import SideBar from "./commonComponent/sideBar";
import { Link } from "react-router-dom";
import { useQuery } from "@tanstack/react-query";
import {
  accptMerchant,
  deleteUser,
  merchantListing,
  ratingData,
  totalOverview,
  userData,
} from "../apiServices/home/homeHttpService";
import { showAlert } from "./commonComponent/alertManager";
import { useAlert } from "react-alert";
import { RotatingLines } from "react-loader-spinner";

function Dashboard() {
  const [loader, setLoader] = useState(false);
  const [delId, setDelId] = useState("");
  const [reason, setReason] = useState("");
  const alert = useAlert();

  const { data: count } = useQuery({
    queryKey: ["Count"],
    queryFn: totalOverview,
    onError: (error) => {
      console.log(error);
    },
    select: (data) => data.results,
  });

  const { data: users, refetch } = useQuery({
    queryKey: ["UsersListDb"],

    queryFn: async () => {
      const formData = {
        search: "",
        year: "",
        month: "",
        page: 1,
        pageSize: 5,
      };
      return userData(formData);
    },
    onError: (error) => {
      console.log(error);
    },
    select: (data) => data.results.users,
  });

  const { data: newMerchants, refetch: refetch2 } = useQuery({
    queryKey: ["newMerchantListDb"],
    queryFn: async () => {
      const formData = {
        search: "",
        year: "",
        month: "",
        page: 1,
        pageSize: 5,
      };
      return merchantListing(formData);
    },
    onError: (error) => {
      console.log(error);
    },
    select: (data) => data.results.merchants,
  });

  const { data: rating } = useQuery({
    queryKey: ["ratingListDb"],
    queryFn: async () => {
      const formData = {
        search: "",
        year: "",
        month: "",
        page: 1,
        pageSize: 6,
      };
      return ratingData(formData);
    },
    onError: (error) => {
      console.log(error);
    },
    select: (data) => data.results.ratings,
  });

  const acceptMerchant = async (id, status) => {
    if (status === "Declined" && !reason) {
      showAlert(alert, "Please add a reason", { timeout: 3000 });
      return;
    }
    setLoader(true);
    const formData = {
      action: status,
      reason: reason,
    };
    const response = await accptMerchant(formData, id);
    if (!response.error) {
      setLoader(false);
      if (status === "Declined") {
        document.getElementById("popUpClose").click();
      }
      showAlert(alert, response.message, { timeout: 3000 });
      refetch2();
    } else {
      setLoader(false);
      showAlert(alert, response.message, { timeout: 3000 });
    }
  };

  const delUser = async (id) => {
    setLoader(true);
    const response = await deleteUser(id);
    if (!response.error) {
      setLoader(false);
      document.getElementById("popUpClose2").click();
      showAlert(alert, response.message, { timeout: 3000 });
      refetch();
    } else {
      setLoader(false);
      showAlert(alert, response.message, { timeout: 3000 });
    }
  };

  console.log(count, users, newMerchants);

  return (
    <>
      <Header />

      <div className="admin_main">
        <SideBar />

        <div className="admin_contentpart">
          <div className="row mb-md-4">
            <div className="col-lg-3 col-md-6 col-12">
              <Link to="" className="staticss_box">
                <span>Total Users</span>
                <div className="static_data">
                  <strong>{count?.totalUser || 0}</strong>
                </div>
              </Link>
            </div>
            <div className="col-lg-3 col-md-6 col-12">
              <Link to="" className="staticss_box staticss_box1">
                <span>Total merchants </span>
                <div className="static_data">
                  <strong>{count?.totalMerchant || 0}</strong>
                </div>
              </Link>
            </div>
            <div className="col-lg-3 col-md-6 col-12">
              <Link to="" className="staticss_box staticss_box1">
                <span>Total Rating &amp; Review</span>
                <div className="static_data">
                  <strong>{count?.totalRatingReview || 0}</strong>
                </div>
              </Link>
            </div>
            <div className="col-lg-3 col-md-6 col-12">
              <Link to="" className="staticss_box">
                <span>Total Revenue</span>
                <div className="static_data">
                  <strong>{count?.totalRevenue || 0} ED</strong>
                </div>
              </Link>
            </div>
          </div>
          <div className="row comman_design mx-0">
            <div className="col-12">
              <div className="row align-items-center justify-content-between pt-md-4 pt-3 px-md-3 px-2">
                <div className="col-md-auto mb-md-0 mb-3">
                  <div className="headleft">
                    <h2>Recent Users</h2>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-12">
              <div className="row comman_table pt-md-3 pb-md-4 pb-3 px-md-3 px-2">
                <div className="col-12 position-relative">
                  <div className="table-responsive">
                    <table className="table table-hover">
                      <thead>
                        <tr>
                          <th>S.No.</th>
                          <th>Image</th>
                          <th>User Name</th>
                          <th>Email</th>
                          <th>Mobile Number</th>
                          <th>Cuisines</th>
                          <th>Status</th>
                          <th>Action</th>
                        </tr>
                      </thead>
                      <tbody>
                        {users?.map((user, index) => (
                          <tr key={user._id}>
                            <td>{index + 1}</td>
                            <td>
                              <div className="user-img">
                                <img
                                  src={
                                    user.profile || "../assets/img/user1.png"
                                  }
                                  alt=""
                                />
                              </div>
                            </td>
                            <td>{user.name}</td>
                            <td>{user.email}</td>
                            <td>{user.mobileNumber}</td>
                            <td>{user.prefers?.join(", ")}</td>
                            <td>
                              <span
                                className={
                                  user.status
                                    ? "status_accepted"
                                    : "status_declained"
                                }
                              >
                                {user.status ? "Active" : "Inactive"}
                              </span>
                            </td>
                            <td>
                              <div className="d-flex">
                                <Link
                                  to={`/admin/user-details/${user._id}`}
                                  className="Table_btn me-2"
                                >
                                  <i className="fa fa-eye" />
                                </Link>
                                <Link
                                  to=""
                                  className="Table_btn"
                                  onClick={() => setDelId(user._id)}
                                  data-bs-toggle="modal"
                                  data-bs-target="#popUp2"
                                >
                                  <i className="fa fa-trash" />
                                </Link>
                              </div>
                            </td>
                          </tr>
                        ))}
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="row mt-4">
            <div className="col-md-12">
              <div className="row comman_design mx-0">
                <div className="py-3 px-3 ">
                  <div className="headleft">
                    <h2>New merchant</h2>
                  </div>
                </div>
                <div className="col-12 px-0">
                  <div className="row comman_table pt-md-0 pb-md-4 pb-3 px-md-3 px-2">
                    <div className="col-12 normal-search">
                      <div className="table-responsive">
                        <table className="table">
                          <thead>
                            <tr>
                              <th>S.No.</th>
                              <th>Image</th>
                              <th>Name</th>
                              <th>Email</th>
                              <th>Mobile Number</th>
                              <th>Action</th>
                            </tr>
                          </thead>
                          <tbody>
                            {newMerchants?.map((user, index) => (
                              <tr key={user._id}>
                                <td>{index + 1}</td>
                                <td>
                                  <img
                                    src={
                                      user.profile || "../assets/img/user1.png"
                                    }
                                    alt=""
                                  />
                                </td>
                                <td>{user.name}</td>
                                <td>{user.email}</td>
                                <td>{user.mobileNumber}</td>
                                <td>
                                  <div className="d-flex justify-content-start gap-2">
                                    <Link
                                      to={`/admin/merchant-details/${user._id}`}
                                      className="bg-main px-2 status_accepted_bg text-white"
                                    >
                                      View
                                    </Link>
                                    <Link
                                      to="#"
                                      className="status_accepted_bg"
                                      onClick={() =>
                                        acceptMerchant(user._id, "Accept")
                                      }
                                    >
                                      Accept
                                    </Link>
                                    <Link
                                      to="#"
                                      className="status_declained_bg"
                                      onClick={() => setDelId(user._id)}
                                      data-bs-toggle="modal"
                                      data-bs-target="#popUp"
                                    >
                                      Decline
                                    </Link>
                                  </div>
                                </td>
                              </tr>
                            ))}
                          </tbody>
                        </table>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="row comman_design mx-0 mt-4">
            <div className="col-12">
              <div className="row align-items-center justify-content-between py-md-4 py-3 px-md-3 px-2">
                <div className="col-md-auto mb-md-0 mb-3">
                  <div className="headleft">
                    <h2>Rating &amp; Review</h2>
                  </div>
                </div>
                <div className="col-md-6 d-flex align-items-center justify-content-end">
                  <div className="searchh_box position-relative">
                    <input
                      className="form-control"
                      type="search"
                      placeholder="Search"
                    />
                    <button>
                      <i className="fas fa-search" />
                    </button>
                  </div>
                  <div className="dropdown filterdropdown filterdropdown2">
                    <button
                      className="btn btn-secondary"
                      type="button"
                      data-bs-toggle="dropdown"
                      aria-expanded="false"
                    >
                      <i className="fas fa-filter" />
                    </button>
                    <div className="dropdown-menu">
                      <form className="form-design row mx-0">
                        <div className="col-12 form-group">
                          <label>Year</label>
                          <select
                            className="form-select"
                            aria-label="Default select example"
                          >
                            <option selected>2001</option>
                            <option value={1}>2002</option>
                            <option value={2}>2003</option>
                            <option value={2}>2004</option>
                            <option value={2}>2005</option>
                            <option value={2}>2006</option>
                            <option value={2}>2007</option>
                            <option value={2}>2008</option>
                            <option value={2}>2009</option>
                            <option value={2}>2010</option>
                          </select>
                        </div>
                        <div className="col-12 form-group">
                          <label>Months</label>
                          <select
                            className="form-select"
                            aria-label="Default select example"
                          >
                            <option selected>Jan</option>
                            <option value={1}>Feb</option>
                            <option value={2}>Mar</option>
                            <option value={2}>Apr</option>
                            <option value={2}>May</option>
                            <option value={2}>Jun</option>
                            <option value={2}>July</option>
                            <option value={2}>August</option>
                            <option value={2}>Sep</option>
                            <option value={2}>Oct</option>
                            <option value={2}>Nov</option>
                            <option value={2}>Dec</option>
                          </select>
                        </div>
                      </form>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div className="col-12">
              <div className="row comman_table pt-md-0 pb-md-4 pb-3 px-md-3 px-2">
                {rating?.map((item, index) => (
                  <div className="col-lg-4 col-md-6 col-12 mt-lg-0 mt-4">
                    <div className="cards shadow py-4 px-4 rounded-3">
                      <div>
                        <div className="d-flex flex-wrap justify-content-between">
                          <div className="d-flex gap-3 w-75">
                            <div className="dash-review-pro-img">
                              <img
                                src="../assets/img/spicy1.jpg"
                                alt=""
                                className="w-100 h-100"
                              />
                            </div>
                            <div>
                              <h2 className="leading-1 fw-semibold">
                                Tuna soup spinach with himalaya salt
                              </h2>
                              <p className="leading-1 fw-light">
                                30 minutes ago
                              </p>
                            </div>
                          </div>

                          <div className="select-dropdown">
                            <button
                              type="button"
                              className="btn select-dropdown__button"
                            >
                              <i className="fa fa-ellipsis-v main-color leading-1 cursor-pointer view-review-dash" />
                            </button>
                            <div className="select-dropdown__list">
                              <Link
                                to="/admin/rating-review-details"
                                className="fs-6 fw-semibold mb-0 cursor-pointer text-decoration-none"
                              >
                                View
                              </Link>
                            </div>
                          </div>
                        </div>
                        <div>
                          <p className="leading-1 fw-light">
                            A very fine addition to the not over plentiful
                            supply of good restaurants in this part of west
                            London.
                          </p>
                        </div>
                      </div>
                      <div className="bg-main py-3 px-3 rounded-3">
                        <div className="d-flex gap-2 align-items-center justify-content-between">
                          <div className="dash-review-user-img">
                            <img
                              src="../assets/img/user1.png"
                              alt=""
                              className="w-100 h-100"
                            />
                          </div>
                          <div>
                            <p className="leading-1 fw-bold text-white mb-2">
                              Jhon Doe
                            </p>
                            <div className="d-flex gap-2">
                              <i className="fa fa-star text-white leading-1" />
                              <i className="fa fa-star text-white leading-1" />
                              <i className="fa fa-star text-white leading-1" />
                              <i className="fa fa-star text-white leading-1" />
                              <i className="fa fa-star leading-1" />
                            </div>
                          </div>
                          <div className="mt-4">
                            <h6 className="fs-4 fw-bold text-white mb-0">
                              4.9
                            </h6>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>
      </div>

      <div
        className="modal fade popUp "
        id="popUp2"
        data-bs-backdrop="static"
        data-bs-keyboard="false"
        tabIndex={-1}
        aria-labelledby="staticBackdropLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog modal-dialog-centered">
          <div className="modal-content">
            <div className="modal-body border-0 py-4 px-3">
              <div className="popUp_main text-center">
                <img
                  className="main_icon"
                  src="../assets/img/reject_imgg.webp"
                  alt=""
                />
                <h3>Are You sure, you want to delete?</h3>
                <div className="row mx-0 mt-4 mb-2">
                  <div className="col-6">
                    <Link
                      className="bg-main  status_accepted_bg w-100 d-block"
                      data-bs-dismiss="modal"
                      to=""
                      onClick={() => setDelId("")}
                      id="popUpClose2"
                    >
                      No
                    </Link>
                  </div>
                  <div className="col-6">
                    <button
                      className="bg-custom-secondary status_accepted_bg w-100"
                      onClick={() => delUser(delId)}
                      disabled={loader}
                    >
                      {loader ? (
                        <>
                          <span className="me-2">Wait</span>
                          <RotatingLines
                            strokeColor="white"
                            strokeWidth="5"
                            animationDuration="0.75"
                            width="18"
                            visible={true}
                          />
                        </>
                      ) : (
                        "Yes"
                      )}
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div
        className="modal fade popUp "
        id="popUp"
        data-bs-backdrop="static"
        data-bs-keyboard="false"
        tabIndex={-1}
        aria-labelledby="staticBackdropLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog modal-dialog-centered">
          <div className="modal-content">
            <div className="modal-body border-0 py-4 px-3">
              <div className="form-design">
                <div className="col-md-12">
                  <div className="form-group">
                    <label className="mb-2">
                      Give a Reason To Delcine User
                    </label>
                    <textarea
                      className="form-control"
                      name="reason"
                      id="reason"
                      value={reason}
                      onChange={(e) => setReason(e.target.value)}
                      style={{ height: "120px" }}
                    />
                  </div>
                </div>

                <div className="row mt-4 mb-2 text-center">
                  <div className="col-6">
                    <Link
                      className="bg-main  status_accepted_bg w-100 d-block"
                      data-bs-dismiss="modal"
                      to=""
                      onClick={() => {
                        setDelId("");
                        setReason("");
                      }}
                      id="popUpClose"
                    >
                      Cancel
                    </Link>
                  </div>
                  <div className="col-6">
                    <button
                      className="bg-custom-secondary status_accepted_bg status_declained_bg w-100"
                      onClick={() => acceptMerchant(delId, "Declined")}
                      disabled={loader}
                    >
                      {loader ? (
                        <>
                          <span className="me-2">Wait</span>
                          <RotatingLines
                            strokeColor="white"
                            strokeWidth="5"
                            animationDuration="0.75"
                            width="18"
                            visible={true}
                          />
                        </>
                      ) : (
                        "Decline"
                      )}
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Dashboard;
